import app from "flarum/forum/app";
import load from "external-load";
import PDFObject from "PDFObject";
import { DPlayerData,ArtPlayerData } from "./extensions";
import CommentPost from "flarum/forum/components/CommentPost";
import { extend } from "flarum/common/extend";
import downloadButtonInteraction from "./downloadButtonInteraction";
const Artplayer = require('artplayer');

const loadScript = async extension => {
  if (extension.loaded) {
    return;
  }
  await load.js(extension.url);

  extension.loaded = true;
};

const loadDPlayer = async () => await loadScript(DPlayerData);

const initDPlayer = async () => await loadDPlayer();

const loadArtPlayer = async () => await loadScript(ArtPlayerData);

const initArtPlayer = async () => await loadArtPlayer();

const createDpInstance = (
  container
) => {
  const videoUrl = container.dataset.url;
  const lang = navigator.language.toLowerCase();

  const art = new Artplayer({
    container: container,
    theme: "#23ade5",
    volume: 0.7,
    id: Buffer.from(videoUrl, 'utf-8').toString('base64'),
    loop: false,
    lang: lang,
    live: false,
    mutex: true,
    setting: true,
    autoSize: true,
    muted: false,
    autoplay: false,
    hotkey: true,
    useSSR: true,
    backdrop: true,
    whitelist: ['*'],
    flip: true,
    fullscreen: true,
    fullscreenWeb: true,
    miniProgressBar: true,
    moreVideoAttr: {
      crossOrigin: 'anonymous',
    },
    url: videoUrl,
    contextmenu: [
      {
        html: "GBCL UplQCloud",
        click: window.open("https://github.com/GBCLStudio/FoF-Upload-Qcloud"),
      },
    ],
    icons: {
      loading: '<img src="https://artplayer.org/assets/img/ploading.gif">',
      state: '<img width="150" heigth="150" src="https://artplayer.org/assets/img/state.svg">',
      indicator: '<img width="16" heigth="16" src="https://artplayer.org/assets/img/indicator.svg">',
    },
  });
}

const createPdfInstance = container => {
  const pdfResUrl = container.dataset.url,
    options = {
      height: "440px",
      fallbackLink: `<p>${app.translator.trans(
        "gbcl-fof-upload-qcloud.forum.pdf.fallbackNotice1"
      )}: <a href='${pdfResUrl}'>${app.translator.trans(
        "gbcl-fof-upload-qcloud.forum.pdf.fallbackNotice2"
      )}</a></p>`,
    };

  PDFObject.embed(pdfResUrl, container, options);
};

app.initializers.add("gbcl-fof-upload-qcloud", () => {
  downloadButtonInteraction();

  extend(CommentPost.prototype, "refreshContent", () => {
    const dpContainers = this.element.querySelectorAll(
        ".qcloud-dplayer-container"
      ),
      pdfContainers = this.element.querySelectorAll('.qcloud-pdf-container');

    if (dpContainers.length) {
      initArtPlayer()
        .then(() => {
          for (const i of dpContainers) {
            if (i.children.length) {
              continue;
            }

            createDpInstance(i);
          }
        })
        .catch(err => console.log(err));
    }

    if (pdfContainers.length) {
      for (const i of pdfContainers) {
        if (i.children.length) continue;

        createPdfInstance(i);
      }
    }
  });
});